html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  background-color: #ebe7de;
}

p {
  color: #000000;
  font-size: 1.8rem;
  line-height: 2;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1, h2, .heading-blanco, h3 {
  font-family: "Raleway", sans-serif;
  margin: 0 0 5rem 0;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

main h2, main .heading-blanco {
  margin-bottom: 0;
}

h2, .heading-blanco {
  font-size: 3.6rem;
  color: #5E311E;
}
h2 span, .heading-blanco span {
  color: #C39C63;
  font-size: 1.8rem;
  display: block;
}
h2::after, .heading-blanco::after {
  content: "";
  display: block;
  width: 10rem;
  height: 10rem;
  margin: 0 auto;
  background-image: url(../img/cafe.svg);
  background-size: 10rem;
}

.heading-blanco {
  color: #FFFFFF;
}
.heading-blanco span {
  color: #FFFFFF;
}
.heading-blanco::after {
  display: none;
}

h3 {
  font-size: 3.2rem;
}

