@import //van incluirlos y usar su contenido*/
.encabezado {
  padding: 5rem 0;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 768px) {
  .encabezado {
    min-height: 65rem;
  }
}
.encabezado::before {
  content: "";
  background-color: #000000;
  position: absolute;
  top: 0; /*todos los 0 cubre todo el espacio*/
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0.6;
}
.encabezado .contenido-header {
  position: relative;
}
.encabezado .contenido-header h1 {
  text-align: center;
  color: #FFFFFF;
  text-transform: uppercase;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .encabezado .contenido-header h1 {
    margin-top: 10rem;
  }
}

.header-inicio {
  background-image: url(../img/header_inicio.jpg);
}

.header-nosotros {
  background-image: url(../img/header_nosotros.jpg);
}

.header-proceso {
  background-image: url(../img/header_proceso.jpg);
}

.header-menu {
  background-image: url(../img/header_menu.jpg);
}

.header-galeria {
  background-image: url(../img/header_galeria.jpg);
}

.header-contacto {
  background-image: url(../img/header_contacto.jpg);
}

.nav-principal {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .nav-principal {
    flex-direction: row;
    gap: 1rem;
    margin-top: 0;
  }
}
.nav-principal a {
  color: #FFFFFF;
  font-size: 2rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .nav-principal a {
    margin-bottom: 0;
  }
}
.nav-principal a:hover {
  color: #C39C63;
}
.nav-principal a.activo {
  color: #C39C63;
}

.barra {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    padding-top: 3rem;
  }
}
.barra .logo {
  width: 50%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .barra .logo {
    width: 20rem;
    margin: 0;
  }
}

.contenido-principal {
  background-color: #FFFFFF;
  padding: 5rem 2rem;
}
@media (min-width: 768px) {
  .contenido-principal {
    padding: 10rem;
    margin-top: -10rem;
    position: relative;
    border-radius: 1rem;
  }
}

.iconos {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .iconos .listado-iconos {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.iconos .listado-iconos li img {
  margin: 0 auto;
  width: 12rem;
}
.iconos .listado-iconos li p {
  font-family: "Bona Nova", serif;
  font-size: 3.2rem;
  color: #5E311E;
}

.galeria {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 992px) {
  .galeria {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.galeria .imagen img {
  height: 30rem;
  width: 30rem;
  object-fit: cover;
}
@media (min-width: 992px) {
  .galeria .imagen:nth-child(1) {
    grid-row: 1/3;
  }
  .galeria .imagen:nth-child(1) img {
    height: 62rem;
    object-position: center;
  }
  .galeria .imagen:nth-child(2) {
    grid-column: 2/4;
  }
  .galeria .imagen:nth-child(2) img {
    width: 62rem;
  }
  .galeria .imagen:nth-child(4) {
    grid-row: 2/4;
  }
  .galeria .imagen:nth-child(4) img {
    height: 62rem;
  }
  .galeria .imagen:nth-child(5) {
    grid-column: 3/5;
  }
  .galeria .imagen:nth-child(5) img {
    width: 62rem;
  }
  .galeria .imagen:nth-child(6) {
    grid-column: 3/5;
  }
  .galeria .imagen:nth-child(6) img {
    width: 62rem;
  }
  .galeria .imagen:nth-child(7) {
    grid-row: 3/5;
  }
  .galeria .imagen:nth-child(7) img {
    height: 62rem;
  }
  .galeria .imagen:nth-child(8) {
    grid-column: 2/5;
  }
  .galeria .imagen:nth-child(8) img {
    width: 94rem;
  }
  .galeria .imagen:nth-child(9) {
    grid-column: 1/3;
  }
  .galeria .imagen:nth-child(9) img {
    width: 62rem;
  }
  .galeria .imagen:nth-child(10) {
    grid-column: 3/5;
  }
  .galeria .imagen:nth-child(10) img {
    width: 62rem;
  }
}

.nuestro-menu {
  background-image: url(../img/menu_arriba.svg), url(../img/menu_abajo.svg), linear-gradient(to top, #5E311E, #5E311E);
  background-repeat: no-repeat;
  background-position: center 5rem, center 92%, 100%;
  background-size: 200%;
  padding: 15rem 5rem 20rem 5rem;
}
@media (min-width: 768px) {
  .nuestro-menu {
    padding: 15rem 0 20rem 0;
    background-size: 100%;
  }
}
@media (min-width: 992px) {
  .nuestro-menu {
    background-size: 50%, 50%, 100%;
  }
}

@media (min-width: 768px) {
  .grid-menu {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.menu h3 {
  color: #FFFFFF;
}
.menu li {
  display: flex;
  justify-content: space-between;
}
.menu li p {
  color: #FFFFFF;
  font-size: 2.2rem;
  line-height: 1.5;
  margin: 0 0 2rem 0;
}
.menu li p span {
  display: block;
  color: #C39C63;
}
.menu li .precio {
  font-size: 4rem;
  font-weight: 700;
}

.nuestros-testimoniales {
  padding: 10rem 2rem;
}
@media (min-width: 768px) {
  .nuestros-testimoniales {
    padding: 10rem;
  }
}
.nuestros-testimoniales .testimonial {
  background-color: #ebe7de;
  padding: 2rem 4rem;
  max-width: 50rem;
  margin: 0 auto;
  border-radius: 1rem;
}
.nuestros-testimoniales .testimonial .autor {
  text-align: right;
  font-size: 2.4rem;
  color: #5E311E;
  font-family: "Bona Nova", serif;
  font-weight: bold;
}

.footer {
  background-color: #5E311E;
}
.footer .contenido-footer {
  padding: 5rem;
}
@media (min-width: 768px) {
  .footer .contenido-footer {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.footer .copyright {
  text-align: center;
  margin: 0;
  padding: 2rem;
  color: #FFFFFF;
}

.footer-box {
  font-family: "Bona Nova", serif;
  text-align: center;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .footer-box {
    margin-bottom: 0;
  }
}
.footer-box h3 {
  font-family: "Bona Nova", serif;
  margin: 0 0 1rem 0;
  font-size: 2.4rem;
  color: #5E311E;
}
.footer-box p, .footer-box a {
  font-size: 2rem;
  color: #000000;
  margin: 0;
}

@media (min-width: 992px) {
  .contendio-nosotros {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}
.contendio-nosotros .imagen {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .contendio-nosotros .imagen {
    margin: 0rem 2rem 5rem 0rem;
  }
}
@media (min-width: 992px) {
  .contendio-nosotros .imagen {
    margin: 0rem 2rem 0rem 0;
  }
}
.contendio-nosotros .imagen img {
  border: 3rem solid #FFFFFF;
  box-shadow: 9px 18px 5px -7px rgba(0, 0, 0, 0.35);
  transition-duration: 0.3s;
}
@media (min-width: 768px) {
  .contendio-nosotros .imagen img {
    transform: rotate(-6deg);
  }
  .contendio-nosotros .imagen img:hover {
    transform: scale(1.05) rotate(-6deg);
    cursor: pointer;
  }
}
.contendio-nosotros .texto blockquote {
  margin: 0;
  padding: 1rem;
  border-left: 0.5rem solid #5E311E;
  font-size: 2rem;
}

.proceso-cafe {
  margin-bottom: 5rem;
  /*
      &:nth-child(4){
          .texto{
              order: 1; //con esto es posible agrupar los elementos en order
          }

          .imagen{
              order: 2; //con esto es posible agrupar los elementos en order
          }
      }*/
}
@media (min-width: 768px) {
  .proceso-cafe {
    display: flex;
    gap: 5rem;
    align-items: center;
  }
}
.proceso-cafe:last-of-type {
  margin-bottom: -10rem;
}
.proceso-cafe:nth-child(even) .texto {
  order: 1;
}
.proceso-cafe:nth-child(even) .imagen {
  order: 2;
}
.proceso-cafe .imagen, .proceso-cafe .texto {
  flex: 1;
}
.proceso-cafe .texto {
  margin-top: 1rem;
}
.proceso-cafe .texto h3 {
  margin: 0;
  text-align: left;
  color: #5E311E;
}
.proceso-cafe .texto h3::after {
  content: "";
  display: block;
  width: 50%;
  height: 5px;
  background-color: #C39C63;
}

.contenido-menu {
  background-image: url(../img/cafe.jpg);
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: 100%;
  aspect-ratio: 3/7;
}
@media (min-width: 480px) {
  .contenido-menu {
    aspect-ratio: 3/5;
  }
}
@media (min-width: 480px) {
  .contenido-menu {
    aspect-ratio: 3/4;
  }
}
@media (min-width: 992px) {
  .contenido-menu {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    aspect-ratio: 16/6;
    background-size: 40%;
    background-position: bottom right;
  }
}
.contenido-menu .texto p {
  font-size: 2rem;
}

@media (min-width: 768px) {
  .contenido-contacto {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    background-image: url(../img/granos_cafe.jpg);
    background-repeat: no-repeat;
    background-position: right center;
    background-size: 40%;
  }
}

.formulario .campo {
  margin-bottom: 2rem;
}
.formulario .campo:last-of-type {
  margin-bottom: 0;
}
.formulario .campo label {
  text-transform: uppercase;
  display: block;
  font-size: 2rem;
  font-weight: 700;
  text-align: left;
}
.formulario .campo input:not([type=submit]), .formulario .campo select {
  width: 100%;
  padding: 1rem;
  text-align: left;
  border: 0.5rem solid #5E311E;
}
.formulario .campo input[type=submit] {
  background-color: #5E311E;
  width: 100%;
  border: none;
  padding: 2rem;
  color: #FFFFFF;
  font-size: 2.2rem;
  font-weight: 700;
  text-transform: uppercase;
  transition-duration: 0.3s;
}
.formulario .campo input[type=submit]:hover {
  cursor: pointer;
  color: #C39C63;
  transform: scale(0.9);
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.4);
}
@media (min-width: 768px) {
  .formulario .campo input[type=submit] {
    width: 50%;
    display: flex;
    justify-content: center;
  }
}

.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #5E311E;
}

.margin-negativo-10 {
  margin-top: -10rem;
}

.bg-white {
  background-color: #FFFFFF;
}

.padding-bottom-0 {
  padding-bottom: 0;
}

